<template>
  <div class="chat-window-inner">
    <div class="memo-header">
      <span class="memo-title"></span>
    </div>
    <div class="chat-messages" ref="msgListRef">
      <div v-for="(msg, idx) in messages" :key="msg.id || idx" class="message-row">
        <div class="msg-meta">
          <span class="msg-user">{{ getUserName(msg) }}</span>
          <span class="msg-time">{{ formatTime(msg.time) }}</span>
        </div>
        <div class="msg-content">
          <template v-if="isImage(msg.text)">
            <img :src="msg.text" class="msg-img" @click="showImg(msg.text)" style="cursor:pointer" />
          </template>
          <template v-else>
            <span>{{ msg.text }}</span>
          </template>
        </div>
      </div>
    </div>
    <div class="chat-input-bar">
      <n-input
        v-model:value="input"
        type="textarea"
        placeholder="..."
        :autosize="{ minRows: 1, maxRows: 4 }"
        @keydown.enter.exact.prevent="sendMessage"
        class="chat-input-box"
      />
      <n-button type="primary" @click="sendMessage" :disabled="!input.trim()" class="send-btn"></n-button>
    </div>
    <n-modal v-model:show="imgPreview.show" preset="card" class="img-preview-modal" :mask-closable="true" style="text-align:center">
      <img :src="imgPreview.url" style="max-width:90vw;max-height:80vh;object-fit:contain;" />
    </n-modal>
  </div>
</template>

<script>
import { ref, watch, nextTick, onMounted, onBeforeUnmount } from 'vue'
import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'
export default {
  name: 'ChatWindow',
  props: {
    user: Object,
    messages: Array,
    allUsers: Array
  },
  emits: ['sendMessage', 'receiveMessage'],
  setup(props, { emit }) {
    const input = ref('')
    const msgListRef = ref(null)
    let stompClient = null
    let subscriptions = {}
    const imgPreview = ref({ show: false, url: '' })

    // 
    const getUserName = (msg) => {
      if (msg.self) {
        return ''
      }
      // username
      return props.user ? props.user.username : ''
    }
    // 
    const formatTime = (t) => {
      if (!t) return ''
      const d = new Date(t)
      return d.toLocaleString()
    }
    // 
    const isImage = (text) => {
      return typeof text === 'string' && text.startsWith('https://wework')
    }
    // 
    const showImg = (url) => {
      imgPreview.value.url = url
      imgPreview.value.show = true
    }

    // 
    const subscribeAllUsers = (userList) => {
      Object.values(subscriptions).forEach(sub => sub.unsubscribe && sub.unsubscribe())
      subscriptions = {}
      if (stompClient && stompClient.connected && userList && userList.length) {
        userList.forEach(user => {
          if (user && user.id) {
            subscriptions[user.id] = stompClient.subscribe(`/topic/message/${user.id}`, (msg) => {
              const data = typeof msg.body === 'string' ? JSON.parse(msg.body) : msg.body
              emit('receiveMessage', {
                userId: user.id,
                text: data.msg,
                self: data.type === 1,
                id: data.id,
                time: data.time || Date.now()
              })
              if (props.user && props.user.id === user.id) {
                nextTick(() => {
                  if (msgListRef.value) {
                    msgListRef.value.scrollTop = msgListRef.value.scrollHeight
                  }
                })
              }
            })
          }
        })
      }
    }

    // WebSocket
    const connectWebSocket = () => {
      stompClient = new Client({
        brokerURL: undefined,
        webSocketFactory: () => new SockJS('/ws'),
        reconnectDelay: 2000,
        onConnect: () => {
          if (props.allUsers) {
            subscribeAllUsers(props.allUsers)
          }
        }
      })
      stompClient.activate()
    }

    // 
    const sendMessage = () => {
      if (input.value.trim() && stompClient && stompClient.connected && props.user) {
        stompClient.publish({
          destination: '/app/message',
          body: JSON.stringify({
            msg: input.value,
            userId: props.user.id,
            time: Date.now()
          })
        })
        emit('sendMessage', {
          text: input.value,
          self: true,
          id: Date.now(),
          userId: props.user.id,
          time: Date.now()
        })
        input.value = ''
        nextTick(() => {
          if (msgListRef.value) {
            msgListRef.value.scrollTop = msgListRef.value.scrollHeight
          }
        })
      }
    }

    onMounted(() => {
      connectWebSocket()
    })
    onBeforeUnmount(() => {
      if (stompClient) stompClient.deactivate()
      Object.values(subscriptions).forEach(sub => sub.unsubscribe && sub.unsubscribe())
    })

    watch(() => props.allUsers, (newUsers) => {
      if (stompClient && stompClient.connected && newUsers) {
        subscribeAllUsers(newUsers)
      }
    }, { immediate: true })

    watch(() => props.user && props.user.id, () => {
      nextTick(() => {
        if (msgListRef.value) {
          msgListRef.value.scrollTop = msgListRef.value.scrollHeight
        }
      })
    })
    watch(() => props.messages, () => {
      nextTick(() => {
        if (msgListRef.value) {
          msgListRef.value.scrollTop = msgListRef.value.scrollHeight
        }
      })
    })

    return { input, sendMessage, msgListRef, getUserName, formatTime, isImage, showImg, imgPreview }
  }
}
</script>

